html {
  box-sizing: border-box; }

*, *::after, *::before {
  box-sizing: inherit; }

.eureka button, .eureka .eureka__layout-chooser .eureka__icon-radio-btns label {
  cursor: pointer; }

/*
http://www.phase2technology.com/blog/exploring-maps-in-sass-3-3/
*/
.visually-hidden, .eureka .eureka__layout-chooser legend, .eureka .eureka__layout-chooser .eureka__icon-radio-btns input[type="radio"] {
  /*https://developer.yahoo.com/blogs/ydn/clip-hidden-content-better-accessibility-53456.html*/
  position: absolute !important;
  clip: rect(1px 1px 1px 1px);
  /* IE6, IE7 */
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0 !important;
  border: 0 !important;
  height: 1px !important;
  width: 1px !important;
  overflow: hidden; }
  body:hover .visually-hidden a, body:hover .eureka .eureka__layout-chooser legend a, .eureka .eureka__layout-chooser body:hover legend a, body:hover .eureka .eureka__layout-chooser .eureka__icon-radio-btns input[type="radio"] a, .eureka .eureka__layout-chooser .eureka__icon-radio-btns body:hover input[type="radio"] a, body:hover .visually-hidden input, body:hover .eureka .eureka__layout-chooser legend input, .eureka .eureka__layout-chooser body:hover legend input, body:hover .eureka .eureka__layout-chooser .eureka__icon-radio-btns input[type="radio"] input, .eureka .eureka__layout-chooser .eureka__icon-radio-btns body:hover input[type="radio"] input, body:hover .visually-hidden button, body:hover .eureka .eureka__layout-chooser legend button, .eureka .eureka__layout-chooser body:hover legend button, body:hover .eureka .eureka__layout-chooser .eureka__icon-radio-btns input[type="radio"] button, .eureka .eureka__layout-chooser .eureka__icon-radio-btns body:hover input[type="radio"] button {
    display: none !important; }

.eureka .eureka__button-bar, .eureka .eureka__tree-bar {
  display: flex; }
  .eureka .eureka__button-bar > *, .eureka .eureka__tree-bar > * {
    margin-right: .5em; }
    .eureka .eureka__button-bar > *:last-of-type, .eureka .eureka__tree-bar > *:last-of-type {
      margin-right: 0; }

.eureka .eureka__button-bar, .eureka .eureka__notice {
  text-align: center; }

